/* ////////////////// NAVBAR /////////////////////////// */

nav a,
nav a:link,
nav a:visited {
  padding-bottom: 1px;
  color: rgb(250, 250, 250) !important;
  border-bottom: 2px solid transparent;
  transition: all 400ms ease;
}
nav a:hover,
nav a:active {
  color: rgb(255, 255, 255);
  border-bottom: 2px solid rgb(221, 104, 149);
}
.navbar-brand:hover {
  border-bottom: 2px solid whitet;
}

